const Tpl = require('./index.tpl');
const Clientpopup =function(option){
		if(!(this instanceof Clientpopup)){
			return new Clientpopup(option);
		}
		let options = {
			selector:'body',
			position:'right',
			okBtnName:'提交',
			tpl:'<div></div>',
			title:'client',
			submit:$.noop,
			ready:$.noop
		}
		this.options = Object.assign({},options,option)
		this.init();
		this.render();
		this.ready();
		this.event();
	}

	Clientpopup.prototype.init=function(){
    	var html = Tpl(this.options);
		$(this.options.selector).find('.client-popup').remove();
		$(this.options.selector).append(html);

	}

	Clientpopup.prototype.render=function(){
		$('.render-content ,this.options.selector').append(this.options.tpl)
	}

	Clientpopup.prototype.ready = function(){
		this.options.ready && this.options.ready()
	}

	Clientpopup.prototype.event=function(){
		const _this = this;
		$('.client-popup').on('click','.client-popup-close-btn',function(){
			_this.close()
		});

		$('.client-popup').on('click','.client-popup-submit-btn',function(){
			_this.submit()
		})

	}

	Clientpopup.prototype.open=function(callback){
		setTimeout(function(){
			$('.client-popup ,this.options.selector').removeClass('animateright')
			$('.client-popup ,this.options.selector').addClass('animateleft');
			callback && callback();
		},10)	
	}

	Clientpopup.prototype.close=function(){
		$('.client-popup ,this.options.selector').removeClass('animateleft')
		$('.client-popup ,this.options.selector').addClass('animateright')
	}

	Clientpopup.prototype.submit=async function(){
		this.options.submit && await this.options.submit()
		this.close()
	}

module.exports = function(option){
	return Clientpopup(option)
}



