// andon台账
var security = require("security");
var util = require('util');
var pop = require('popup');
var tpl = require('../../../views/components/dailyWorkList-right.html')
var requestApi = appConfig.baseEnv;
var andonLogCtrl = {
	page : undefined,
    datatableApi : undefined,
    queryParam : {
        siteCode:requestApi.api.siteCode,
        serviceCode : "GMES-CLIENT-PCS00017",
        languageCode : "zh",
        userCode:"",
        operationCode : "",
        stationCode : "",
        productNum : "",
        productTrackNum:"",
        minSwitchOnDateTime : "",
        maxCreateDateTime : ""
    },
	init: function() {
        $(document).off("keydown");
		util.setBreadcrumb(['报工履历']);
		this.loadPage();
        util.translate('.page[data-page="andonLog"]');
		this.event();
	},
	event: function(){
		var _this = this;
		var param = { 
		    selector:'.baogonglist',
		    tpl:tpl,
		    title:'查询',
		    okBtnName:'查询',
		    ready:function(){
		    	_this.initWorkCenterData();  //渲染工作中心选择下拉框
		        _this.renderTimePicker();  //渲染时间选择按钮输入框
		        _this.renderAndonStatus();   //渲染andon状态多选按钮
		        _this.initStationSlect();
		    },
		    submit:function(){
		    	var param = {
		    			stationCode:$('select[name="stationCode"]').val(),
		    			productNum:$('input[name="productNum"]').val(),
		    			minCreateDateTime:$('input[name="minCreateDateTime"]').val(),
		    			maxCreateDateTime:$('input[name="maxCreateDateTime"]').val()

		    		}
		    	_this.reloadDatatableByParam(param)
			}
		};
		this.popup = pop(param);
		var _this = this;
        // 点击右侧按钮可弹出右侧查询栏
		$(".toggle-right-btn").on("click",function(e){
			console.log()
			_this.popup.open()
			/*Sweet.rightPanelView.router.loadPage('views/andon/andonLog-set.html');
			myApp.openPanel('right');*/
		})
	},
    // 默认加载图表
	loadPage : function(){
        var _this = this;
        var scrollY = (this.page.height-126);
//      require("widget");
//      require("recordTable");
        // 调用封装好的dataTable实例化方法
        this.datatableApi = util.dataTable($('.table-panel table',this.page),{
//       $('.table-panel table',this.page).recordTable({
            "scrollY": scrollY,
            "ordering": false,
            "order" : [],
            "pageLength":8,
            /*"aoColumns": [
              { "sWidth": "5%", "aTargets": [ 0 ] },
              { "sWidth": "20%", "aTargets": [ 0 ] },
              { "sWidth": "20%", "aTargets": [ 0 ] },
              { "sWidth": "20%", "aTargets": [ 0 ] },
              { "sWidth": "10%", "aTargets": [ 0 ] },
              { "sWidth": "10%", "aTargets": [ 0 ] },
              { "sWidth": "5%", "aTargets": [ 0 ] },
              { "sWidth": "5%", "aTargets": [ 0 ] }
            ],*/
            "ajax" : {
                serviceCode : "GMES-CLIENT-PCS00017",
                param : function(){
                    return  _this.queryParam;
                }
            },
            "columns": [
                {
                    "data": "id",
                    "render": function ( data, type, row,meta  ) {
                        return meta["settings"]["_iDisplayStart"] + meta.row+1
                    }
                },
                {
                    "data": "productionSeqNum",
                    "name": "productionSeqNum",
                },
                {
                    "data": "productNum",
                    "name": "productNum"
                },
                {
                    "data": "materialText",
                    "name": "materialText"
                },
                
                /*{
                    "name": "statusECode",
                    "width" : "65px",
                    "render": function ( data, type, row ) {
                        var statusText;
                        var bgColor;
                        switch(row.statusECode){
                            case "1":{
                                statusText = i18next.t("ANDON.T.STATUS_TOUCH");
                                bgColor = "#ef5362";
                                break;
                            }
                            case "3":{
                                statusText = i18next.t("ANDON.T.STATUS_CLOSE");
                                bgColor = "#ffce54";
                                break;
                            }
                            default:
                                statusText = "确认";
                                bgColor = "#27c24c";
                        }
                        return  '<span style="padding:5px 15px;background:' + bgColor + ';border-radius:15px;">' + statusText + '</span>';
                    }
                },*/
                /*{
                    "name": "confirmDateTime",
                    "render": function ( data, type, row ) {
                        var time = Math.abs((row.switchOffDateTime || security.getServerDateTime()) - row.switchOnDateTime)/1000;
                        return formatSeconds(time);
                        //将秒数换成时分秒格式
                        function formatSeconds(value) {
                            var theTime = parseInt(value);// 秒
                            var theTimeMinute = 0;// 分
                            var theTimeHour = 0;// 小时
                            if(theTime > 60) {
                                theTimeMinute = parseInt(theTime/60);
                                theTime = parseInt(theTime%60);
                                if(theTimeMinute > 60) {
                                    theTimeHour = parseInt(theTimeMinute/60);
                                    theTimeMinute = parseInt(theTimeMinute%60);
                                }
                            }
                            var result = i18next.t("COMMON.T.TIME",{
                                hour : parseInt(theTimeHour),
                                minute : parseInt(theTimeMinute),
                                second : parseInt(theTime)
                            })
                            return result;
                        }
                    },
                },*/
                {
                    "data": "quantity",

                    "name": "quantity"
                },
                {
                    "data": "confirmDateTime",
                    "name": "confirmDateTime",
                    "render": function ( data, type, row ) {
                        return formatDate(data);
                        //将秒数换成日期格式 YY-MM-DD hh:mm:ss
                        function formatDate(time) {
                            var tmpDate=new Date(time);
                            var year = tmpDate.getFullYear();
                            var mathon = tmpDate.getMonth() + 1;
                            var day = tmpDate.getDate();
                            var hours = tmpDate.getHours();
                            var minutes = tmpDate.getMinutes();
                            var seconds = tmpDate.getSeconds();
                            return year + '-' + getzf(mathon) + '-' + getzf(day) + ' ' + getzf(hours) + ':' + getzf(minutes) + ':' +getzf(seconds);
                            //补0操作
                            function getzf(num){
                                if(parseInt(num) < 10){
                                    num = '0'+num;
                                }
                                return num;
                            }
                        }
                    },
                },
                {
                    "data": "stationText",
                    "name": "stationText"
                },
                {
                    "data": "operationText",
                    "name": "operationText"
                }
            ]
        })
	},
	searchBtn: function(){
        // 点击查询按钮，取得表单的输入值，然后调用andon台账重新渲染的方法
        $("[name='btnSearch']",this.page).on('click',function(){
            var formData = myApp.formToJSON('#andonSetForm');
            var searchParam = {};
            console.log(formData)
            searchParam.workCenterCode = formData.workCenterCode;
            searchParam.stationCode = formData.stationCode;
            if(formData.typeCode){
                searchParam.typeCode = formData.typeCode.join(",");
            } 
            searchParam.statusECode = formData.statusECode.join(",");
            //searchParam.workCenterCode = "";
            //searchParam.stationCode = "";
            //searchParam.typeText = "";
            //searchParam.statusECode = "";
            searchParam.minSwitchOnDateTime = formData.SwitchOnDateTime.split(" - ")[0];
            searchParam.maxCreateDateTime = formData.SwitchOnDateTime.split(" - ")[1];
            searchParam.minSwitchOffDateTime = formData.SwitchOffDateTime.split(" - ")[0];
            searchParam.maxSwitchOffDateTime = formData.SwitchOffDateTime.split(" - ")[1];
            myApp.closePanel('right');
            andonLogCtrl.reloadDatatableByParam(searchParam);
        });
    },
    //渲染工作中心选择下拉框
    initWorkCenterData: function(){
        var _this = this;
        util.service('GMES-CLIENT-ANDON002',{
            },null,function(response){
                var renderData = { allWorkCenterList : response.data };
                console.log(renderData)
                util.tplManager("allWorkCenterListTpl", renderData, "allWorkCenterListRender");
                $("#allWorkCenterListRender",_this.page).dropkick({
                    // 根据新选择的工作中心，查询站点
                    change : function () {
                        var value = $("#allWorkCenterListRender option:selected",_this.page).val();
                        _this.reInitStationSlect(value);
                    }
                });
            });
    },
    //渲染时间选择按钮输入框
    renderTimePicker: function(){
        app.datetimeSingRender("input.datetime",this.page);
    },
    // 渲染andon状态多选按钮
    renderAndonStatus: function () {
        app.checkboxRender("input[name='statusECode']",this.page);
    },
    //渲染站点选择下拉框--初始化时，渲染空的下拉框
    initStationSlect: function () {
        $("#stationListRender",this.page).dropkick({
            change : function () {}
        });
    },
    //重新渲染站点选择下拉框--根据工作中心查询
    reInitStationSlect: function (workCenterCode) {
        var _this = this;
        util.service('GMES-CLIENT-ANDON003',{
                workCenterCode: workCenterCode
            },null,function(response){
                var stationListData = response.data;
                var renderData = { stationList : stationListData };
                $("#stationListContainer",_this.page).empty();
                $("#stationListContainer",_this.page).html('<select class="form-select-control" id="stationListRender" name="stationCode"><option value=""></option></select>');
                util.tplManager("stationListTpl", renderData, "stationListRender");
                $("#stationListRender",_this.page).dropkick({
                    change : function () {
                        var value = $("#stationListRender option:selected",_this.page).val();
                        _this.renderAndonType(value);
                    }
                });
            });
    },
    // 渲染andon类型多选按钮--初始化时无stationCode，查询渲染所有，后根据选择的站点查询然后渲染
    renderAndonType: function (stationCode) {
        var _this = this;
        util.service('GMES-CLIENT-ANDON004',{
            },null).done(function(response){
                var andonTypeArray = response.data;
                renderAndonTypeByArray(andonTypeArray);
            });
        // 将andon类型3个分组，第一组有label标签，然后渲染
        function renderAndonTypeByArray(data){
            $("#andonTypeRender").empty();
            // 将andon类型数组处理成渲染模板需要的格式
            var length = data.length;
            for(var i = 0; i<length; i++){
                data[i].typeText = data[i].typeText.substring(0,2);
            }
            var groupNum = 0;
            var allTypeGroup = [];
            do{
                var temp = {};
                if(groupNum == 0){
                    temp.showLabel = true;
                }
                var datalength = data.length;
                if(data.length > 3){
                    temp.TypeGroup = data.splice(0,3);
                }else{
                    temp.TypeGroup = data.splice(0,datalength);
                }
                groupNum++;
                allTypeGroup.push(temp);
            }while(data.length > 0)
            var renderData = {
                allTypeGroup :allTypeGroup
            };

            util.tplManager("andonTypeTpl", renderData, "andonTypeRender");
            app.checkboxRender("input[name='typeCode']",_this.page);
    	}
    },
    // 根据查询栏输入的查询参数重新加载图表
    reloadDatatableByParam: function(param) {
        this.queryParam = $.extend({},this.queryParam,param);
        this.datatableApi.ajax.reload();
    }
};

module.exports = andonLogCtrl



